
.pic {
  color: white;
  padding: 5px 6px;
  font-size: 10px;
  font-weight: 400;
  border-radius: 4px;
  background-color: green;
  text-align: center;
  vertical-align: middle;
}
.small-img {
  padding: 1px;
  max-width: 100px;
  height: 65px;
}
@media screen and (min-width: 768px) {
.small-img {
    height: 100px;
}
}
@media screen and (min-width: 425px) {
.small-img {
    height: 80px;
}
}



